<template lang="pug">
  main
    transition(name="fadeup")
      section(class="section-1" v-if="section === 1")
        Title
    transition(name="fadeup")
      section(class="section-2" v-if="section === 2")
        Introduction
    transition(name="fadeup")
      section(class="section-3" v-if="section === 3")
        SkillSet
    transition(name="fadeup")
      section(class="section-4" v-if="section === 4")
        Other
    .circle(:class="`show-section-${section}`")
      CircleAnimation
    .Anchor
      AnchorLink(
        v-bind:nowSection="this.section"
        @setSection="setSection"
      )
</template>

<script>
import Title from '~/components/Title.vue'
import Introduction from '~/components/Introduction.vue'
import SkillSet from '~/components/SkillSet.vue'
import Other from '~/components/Other.vue'
import CircleAnimation from '~/components/CircleAnimation.vue'
import AnchorLink from '~/components/AnchorLink.vue'

export default {
  components: {
    Title,
    Introduction,
    SkillSet,
    Other,
    CircleAnimation,
    AnchorLink
  },
  data() {
    return {
      section: 1,
      wheelFlg: false,
      touchStartY: 0,
      touchStartX: 0,
      touchStartTime: 0,
      touchEndY: 0,
      touchEndX: 0,
      touchEndTime: 0,
    }
  },
  mounted() {
    window.addEventListener('touchstart', (e)=> {
      this.touchStartTime = e.timeStamp
      this.touchStartY = e.touches[0].screenY
      this.touchStartX = e.touches[0].screenX
    })
    window.addEventListener('touchend', (e)=> {
      this.touchEndTime = e.timeStamp
      this.touchEndY = e.changedTouches[0].clientY
      this.touchEndX = e.changedTouches[0].clientX

      this.touchEvents()
    })
    window.addEventListener('wheel', (e) => {
      if(!this.wheelFlg) {
        this.mouseWheel(Math.round(e.deltaY))
      }
    }, true)
    window.addEventListener('keydown', (e)=> {
      if (e.keyCode === 40) {
        this.keydownEvents('up')
      } else if (e.keyCode === 38) {
        this.keydownEvents('down')
      }
    })
  },
  methods: {
    keydownEvents(key) {
      if (key === 'up') {
        this.sectionChange(true)
      } else if (key === 'down') {
        this.sectionChange(false)
      }
    },
    touchEvents() {
      const TOUCH_POSITION_Y_DIFF = this.touchStartY - this.touchEndY
      const TOUCH_POSITION_X_DIFF =
        this.touchStartX > (this.touchEndX + 60) ? false
          : (this.touchStartX + 60) < this.touchEndX ? false
          : true
      const TIMESTAMP_DIFF = this.touchEndTime - this.touchStartTime

      console.log('TOUCH_POSITION_X_DIFF', TOUCH_POSITION_X_DIFF)

      if (TOUCH_POSITION_Y_DIFF > 150 && TIMESTAMP_DIFF > 120 && TOUCH_POSITION_X_DIFF) {
        this.sectionChange(true)
      } else if(TOUCH_POSITION_Y_DIFF < 30 && TIMESTAMP_DIFF > 120 && TOUCH_POSITION_X_DIFF) {
        this.sectionChange(false)
      }
    },
    mouseWheel(wheelY) {
      if (wheelY > 500) {
        this.sectionChange(true)
        this.wheelFlgSetting()
      } else if (wheelY < -500) {
        this.sectionChange(false)
        this.wheelFlgSetting()
      }
    },
    sectionChange(operations) {
      if (operations === true) {
        if(this.section === 4) return;
        this.section += 1
      } else  {
        if(this.section === 1) return;
        this.section -= 1
      }
    },
    wheelFlgSetting() {
      this.wheelFlg = true
      setTimeout(() => {
        this.wheelFlg = false
      }, 500)
    },
    setSection(num) {
      if(this.section !== num) {
        this.section = num
      }
    }
  }
}
</script>

<style lang="stylus">
@import '/assets/styles/index.styl'

main
  position fixed
  top 0
  left 0
  width 100%
  height 100%
  background #000
  overflow hidden

section
  position fixed
  top 0
  left 0
  width 100%
  height 100%
  z-index 2

.circle
  position fixed
  top 50%
  left 50%
  transform translate(-50%, -50%)
  z-index 1
  opacity 0.4
  transition opacity 0.4s ease-out
  pointer-events none
  &.show-section-1
    opacity 1
    transition opacity 0.4s ease-out

.Anchor
  position fixed
  right 30px
  bottom 30px
  z-index 3
  +sp()
    right vw(40)
    bottom vw(40)

</style>
