<template lang="pug">
  .wrapper
    .skillset
      .inner
        .title
          SectionTitle(:title="this.title")
        ul.skillset-list
          li.skillset-item(v-for="item in skillset")
            p.skillset-text {{item}}

</template>

<script>
import SectionTitle from '~/components/SectionTitle.vue'

export default {
  components: {
    SectionTitle
  },
  data() {
    return {
      title: "Skill Set",
      skillset: [
        "HTML5",
        "CSS3",
        "Javascript",
        "jQuery",
        "TweenMax",
        "gulp",
        "webpack",
        "parcel",
        "Pug",
        "EJS",
        "SCSS",
        "Stylus",
        "Vue",
        "Nuxt",
        "Git",
        "MAMP",
        "Photoshop",
        "Illustrator",
        "Slack",
        "Chatwork"
      ]
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~/assets/styles/foundation/index.styl'

.wrapper
  width 100%
  height 100%
  padding 30px
  box-sizing border-box
  +sp()
    padding vw(50)

.skillset
  width 100%
  height 100%
  display flex
  align-items center
  justify-content center

.inner
  width 500px
  +sp()
    width 100%

.title
  margin 0 auto 20px
  +sp()
    margin 0 auto vw(30)

.skillset-list
  width 100%
  text-align center

.skillset-item
  display inline-block
  &:not(:last-of-type)
    p.skillset-text
      &::after
        font-size 16px
        content "/"
        display inline-block
        margin 0 5px
        vertical-align middle
        +sp()
          font-size vw(32)
          margin 0 vw(12)

.skillset-text
  font-size 20px
  line-height 2
  letter-spacing 0.09em
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  +sp()
    font-size vw(36)
    line-height 1.8
    letter-spacing 0.09em

</style>
