<template lang="pug">
  .wrapper
    .other
      .inner
        .title
          SectionTitle(:title="this.title")
        ul.other-list
          Carousel(
            ref="carouselOther"
            :per-page="1"
            :loop="true"
            :paginationActiveColor="`#ffffff`"
            :paginationColor="`#555555`"
            :paginationPadding="5"
            :paginationSize="15"
            v-on:page-change="chageSlide()"
          )
            Slide(v-for="item,index in otherResults")
              li.other-item(:class="`slide-${index+1}`")
                a.other-item-title(:href="item.url" target="_blank" rel="noopener noreferrer") {{item.title}}
                p.ohter-item-lead {{item.lead}}

</template>

<script>
import SectionTitle from '~/components/SectionTitle.vue'
import { Carousel, Slide } from 'vue-carousel'

export default {
  components: {
    SectionTitle,
    Carousel,
    Slide
  },
  data() {
    return {
      title: "Other Results",
      currentSlide: 0,
      otherResults: [
        {
          "title" : "IT",
          "target" : "blank",
          "url" : "http://takuan-poripori.hatenablog.com/",
          "lead" : ""
        },
        {
          "title" : "Qiita",
          "target" : "blank",
          "url" : "https://qiita.com/takuan423",
          "lead" : "WEBQiita"
        },
        {
          "title" : "",
          "target" : "blank",
          "url" : "http://takuan423.github.io/memorandum/",
          "lead" : "jQueryJS"
        }
      ]
    }
  },
  mounted() {
    window.addEventListener('keydown', (e) => this.getKeydown(e), false)
  },
  beforeDestroy() {
    window.removeEventListener('keydown', (e) => this.getKeydown(e), false)
  },
  computed: {
    currentIndex: {
      cache: false,
      get() {
        return this.$refs.carouselOther ? this.$refs.carouselOther.currentPage : 0
      }
    }
  },
  methods: {
    chageSlide() {
      this.currentSlide = this.currentIndex
    },
    getKeydown(e) {
      if (e.keyCode === 37) {
        this.keydownEvents('prev')
      } else if (e.keyCode === 39) {
        this.keydownEvents('next')
      }
    },
    keydownEvents(operations) {
      const current = this.$refs.carouselOther ? this.$refs.carouselOther.currentPage : 0

      if (operations === 'prev') {
        if (current !== 0 && this.$refs.carouselOther) {
          this.$refs.carouselOther.goToPage(current-1)
        } else if (current === 0 && this.$refs.carouselOther) {
          this.$refs.carouselOther.goToPage(2)
        }
      } else {
        if (current !== 2 && this.$refs.carouselOther) {
          this.$refs.carouselOther.goToPage(current+1)
        } else if (current === 2 && this.$refs.carouselOther) {
          this.$refs.carouselOther.goToPage(0)
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~/assets/styles/foundation/index.styl'

.wrapper
  width 100%
  height 100%
  padding 30px
  box-sizing border-box
  position relative
  overflow hidden
  +sp()
    padding vw(50)

.other
  width 100%
  height 100%
  display flex
  align-items center
  justify-content center

.inner
  width 500px
  +sp()
    width 100%

.title
  margin 0 auto 20px
  +sp()
    margin 0 auto vw(30)

.other-list
  width 100%
  margin 0 auto
  +sp()
    width 100%

.other-item
  width 100%
  text-align center
  & + &
    margin-top 20px

.other-item-title
  font-size 20px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  position relative
  padding-right 20px
  display inline-block
  margin 0 auto 15px
  border-bottom 1px solid #fff
  +sp()
    font-size vw(36)
    line-height 1.8
    letter-spacing 0.12em
  &::before
    position absolute
    top 50%
    right 0
    transform translate(0, -50%)
    content ""
    display inline-block
    width 10px
    height 8px
    border 1px solid #fff
  &::after
    position absolute
    top 38%
    right -2px
    transform translate(0, -50%)
    content ""
    display inline-block
    width 10px
    height 8px
    border-top 1px solid #fff
    border-right 1px solid #fff

.ohter-item-lead
  font-size 18px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  +sp()
    font-size vw(28)
    line-height 1.8
    letter-spacing 0.12em

</style>
