<template lang="pug">
  .circles
    .circle(
      v-for="item,index in circles"
      :class='`is-${index+1}`'
      :style='`background-color: ${item.color}`'
    )
</template>

<script>
export default {
  data() {
    return {
      circles: [
        { color: "#ff0000" },
        { color: "#ff007b" },
        { color: "#ff8ce4" },
        { color: "#9f00ef" },
        { color: "#f2e600" },
        { color: "#fffaad" },
        { color: "#00ff3f" },
        { color: "#ad8cdb" },
        { color: "#2b71ff" }
      ],
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~/assets/styles/foundation/index.styl'

.circles
  position fixed
  top 50%
  left 50%
  transform translate(-50%, -50%)
  width 630px
  height 630px
  backface-visibility hidden
  +sp()
    width vw(800)
    height vw(800)
  &::before
    position absolute
    top 50%
    left 50%
    transform translate(-50%, -50%)
    content ""
    display block
    width 625px
    height 625px
    background #000
    border-radius 50%
    z-index 2
    backface-visibility hidden
    +sp()
      width vw(790)
      height vw(790)

.circle
  position absolute
  top 50%
  left 50%
  transform translate(-50%, -50%)
  width 100%
  height 100%
  border-radius 50%
  opacity 0.3
  animation-name border-animations
  animation-timing-function linear
  animation-iteration-count infinite

  &.is-1
    border-radius 50% 80% 60% 90%/45% 90% 40% 85%
    animation-duration 1.1s
  &.is-2
    border-radius 55% 75% 65% 85%/50% 85% 45% 80%
    animation-duration 1.2s
  &.is-3
    border-radius 60% 70% 70% 80%/55% 80% 50% 75%
    animation-duration 1.3s
  &.is-4
    border-radius 65% 65% 75% 75%/60% 75% 55% 70%
    animation-duration 1.4s
  &.is-5
    border-radius 70% 60% 80% 70%/65% 70% 60% 65%
    animation-duration 1.5s
  &.is-6
    border-radius 75% 55% 85% 65%/70% 65% 65% 60%
    animation-duration 1.6s
  &.is-7
    border-radius 80% 50% 90% 60%/75% 60% 70% 55%
    animation-duration 1.7s
  &.is-8
    border-radius 85% 45% 95% 55%/80% 55% 75% 50%
    animation-duration 1.8s
  &.is-9
    border-radius 90% 40% 100% 50%/85% 50% 80% 45%
    animation-duration 1.9s
  &.is-10
    border-radius 95% 35% 105% 45%/90% 45% 85% 50%
    animation-duration 2.0s

@keyframes border-animations
  from
    transform translate(-50%, -50%) rotate(0deg)
  to
    transform translate(-50%, -50%) rotate(360deg)

</style>
