<template lang="pug">
  .wrapper
    .introduction
      .inner
        .title
          SectionTitle(:title="this.title")
        .introduction-slide
          Carousel(
            ref="carouselIntroduction"
            :per-page="1"
            :loop="true"
            :paginationActiveColor="`#ffffff`"
            :paginationColor="`#555555`"
            :paginationPadding="5"
            :paginationSize="15"
            v-on:page-change="chageSlide()"
          )
            Slide
              p.lead(v-html="replaceBr(this.lead)")
            Slide
              p.favorite-title My favorite
              ul.favorite-list
                li.favorite-item(v-for="item,index in favorite")
                  p.key {{Object.keys(item)[0]}}
                  p.value {{Object.values(item)[0]}}

</template>

<script>
import SectionTitle from '~/components/SectionTitle.vue'
import { Carousel, Slide } from 'vue-carousel'

export default {
  components: {
    SectionTitle,
    Carousel,
    Slide
  },
  data() {
    return {
      title: "Introduction",
      currentSlide: 0,
      lead: "\nWEB\nUI/UXWEB\n",
      favorite: [
        { "FOOD": " / " },
        { "COMIC": "ONE PIECE / " },
        { "MOVIE": "2" },
        { "MUSIC": "" },
        { "ANIMAL": "" },
        { "CHARACTER": "" },
        { "GAME": " " },
      ]
    }
  },
  mounted() {
    window.addEventListener('keydown', (e) => this.getKeydown(e), false)
  },
  beforeDestroy() {
    window.removeEventListener('keydown', (e) => this.getKeydown(e), false)
  },
  computed: {
    currentIndex: {
      cache: false,
      get() {
        return this.$refs.carouselIntroduction ? this.$refs.carouselIntroduction.currentPage : 0
      }
    }
  },
  methods: {
    chageSlide() {
      this.currentSlide = this.currentIndex
    },
    getKeydown(e) {
      if (e.keyCode === 37) {
        this.keydownEvents('prev')
      } else if (e.keyCode === 39) {
        this.keydownEvents('next')
      }
    },
    keydownEvents(operations) {
      const current = this.$refs.carouselIntroduction ? this.$refs.carouselIntroduction.currentPage : 0

      if (operations === 'prev') {
        if (current !== 0 && this.$refs.carouselIntroduction) {
          this.$refs.carouselIntroduction.goToPage(current-1)
        } else if (current === 0 && this.$refs.carouselIntroduction) {
          this.$refs.carouselIntroduction.goToPage(1)
        }
      } else {
        if (current !== 1 && this.$refs.carouselIntroduction) {
          this.$refs.carouselIntroduction.goToPage(current+1)
        } else if (current === 1 && this.$refs.carouselIntroduction) {
          this.$refs.carouselIntroduction.goToPage(0)
        }
      }
    },
    replaceBr(text) {
      return text.replace(/[\n\r]/g,"<br>")
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~/assets/styles/foundation/index.styl'

.wrapper
  width 100%
  height 100%
  padding 30px
  box-sizing border-box
  overflow hidden
  +sp()
    padding vw(50)

.introduction
  width 100%
  height 100%
  display flex
  align-items center
  justify-content center

.inner
  width 500px
  +sp()
    width 100%

.title
  margin 0 auto 20px
  +sp()
    margin 0 auto vw(30)

.lead
  font-size 18px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  +sp()
    font-size vw(28)
    line-height 1.8
    letter-spacing 0.12em

.introduction-slide
  width 100%

.favorite-title
  font-size 20px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  margin-bottom 15px
  +sp()
    font-size vw(36)
    line-height 1.8
    letter-spacing 0.12em

.favorite-list
  width 100%

.favorite-item
  width 100%
  display flex
  align-items center
  & + &
    margin-top 10px
    +sp()
      margin-top vw(12)

.key
  display inline-block
  font-size 18px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  &::after
    content ""
    display inline-block
  +sp()
    font-size vw(28)
    line-height 1.8
    letter-spacing 0.12em

.value
  font-size 18px
  line-height 1.5
  letter-spacing 0.09em
  text-align left
  color #fff
  font-family 'Hiragino Maru Gothic ProN', sans-serif
  +sp()
    font-size vw(28)
    line-height 1.8
    letter-spacing 0.12em

</style>
